import React from 'react';

import { ProtoframePubsub } from 'protoframe';

import { Mask } from '../components/Mask';
import { useMemoAsync } from '../hooks/useMemoAsync';
import { useWindowEventListener } from '../hooks/useWindowEventListener';
import { selectElement } from '../protocol/SelectElementProtocol';
import { cssPath } from '../util/ChromiumCssSelectorGenerator';
import { useEnactiveMetadata } from './MetadataProvider';

export type ElementSelectionContext = {
  isActive: boolean | undefined;
};

const pubsub =
  window.opener !== undefined && window.opener !== null
    ? ProtoframePubsub.iframe(selectElement, undefined, {
        targetWindow: window.opener,
      })
    : null;

async function onSelectElement(
  selector: string,
  element: { outerHtml: string; screenshot: string },
) {
  if (pubsub !== null) {
    await pubsub.ask('onSelectElement', { element, selector });
    window.close();
  }
}

const ElementSelectionContextInstance = React.createContext(
  null as unknown as ElementSelectionContext,
);

export const ElementSelectionProvider: React.FC<React.PropsWithChildren> = ({
  children,
}) => {
  const metadata = useEnactiveMetadata();
  const [isActive] = useMemoAsync(async () => {
    if (pubsub === null) {
      return false;
    }
    await pubsub.ping({ timeout: 500 });
    return true;
  }, []);

  React.useEffect(() => {
    void pubsub?.ask('onMetadataChanged', { metadata });
  }, [metadata]);

  const [isShiftPressed, setIsShiftPressed] = React.useState(false);
  const [highlightedElement, setHighlightedElement] =
    React.useState<HTMLElement>();
  const [maskEl, setMaskEl] = React.useState<HTMLElement>();

  const handleKeyboardEvent = React.useCallback(
    (ev: KeyboardEvent) => setIsShiftPressed(ev.shiftKey),
    [],
  );

  const handleClickEvent = React.useCallback(
    (ev: MouseEvent) => {
      if (
        isActive === true &&
        isShiftPressed &&
        highlightedElement !== undefined
      ) {
        // This handling should happen in the capturing phase on the window,
        // which will stop further capturing AND bubbling possibilities. This
        // results in the event having 0 effect on the page, which is what we
        // want when the element select key (shift) is being held down to select
        // the element (rather than normally clicking the button)
        killEvent(ev);
        void onSelectElement?.(resolveSelector(highlightedElement), {
          outerHtml: highlightedElement.outerHTML,
          screenshot: '',
        });
      }
    },
    [isActive, highlightedElement, isShiftPressed],
  );

  const handleMousemoveEvent = React.useCallback(
    (ev: MouseEvent) => {
      if (isActive === true) {
        const elements = document
          .elementsFromPoint(ev.clientX, ev.clientY)
          .filter(
            (el): el is HTMLElement =>
              // Don't include elements that are part of the mask
              el instanceof HTMLElement && !(maskEl?.contains(el) ?? false),
          );
        setHighlightedElement(findSelectableEl(elements) ?? undefined);
      }
    },
    [isActive, maskEl],
  );

  useWindowEventListener('keydown', handleKeyboardEvent);
  useWindowEventListener('keyup', handleKeyboardEvent);
  useWindowEventListener('click', handleClickEvent, { capture: true });
  useWindowEventListener('mousemove', handleMousemoveEvent);

  const context = React.useMemo(() => ({ isActive }), [isActive]);
  return (
    <ElementSelectionContextInstance.Provider value={context}>
      {isActive === true ? (
        <Mask
          MaskProps={{
            style: {
              cursor: highlightedElement !== undefined ? 'pointer' : undefined,
            },
          }}
          active={isShiftPressed}
          expose={highlightedElement}
          maskRef={el => setMaskEl(el ?? undefined)}
        />
      ) : null}
      {children}
    </ElementSelectionContextInstance.Provider>
  );
};

export function useElementSelectorContext(): ElementSelectionContext {
  return React.useContext(ElementSelectionContextInstance);
}

function resolveSelector(el: HTMLElement): string {
  return cssPath(el, {
    idAttrs: ['data-analytics-id', 'analytics-id'],
    optimized: true,
  });
}

function findSelectableEl(els: HTMLElement[]): HTMLElement | null {
  const maxArea = getArea(document.body) / 2;
  const include = [
    'a[href],button,label:has(input),h1,h2,h3,h4,h5,h6,[analytics-id]:not(body),[data-analytics-id]:not(body)',
    'input,[tabindex]:not(body)',
    '[enactive-selectable]',
  ];

  for (const selector of include) {
    for (const el of els) {
      if (getArea(el) < maxArea && el.matches(selector)) {
        return el;
      }
    }
  }

  return null;
}

function getArea(el: HTMLElement): number {
  const { height, width } = el.getBoundingClientRect();
  return height * width;
}

function killEvent(event: Event) {
  event.preventDefault();
  event.stopImmediatePropagation();
}
