// Chromium's source for generating a CSS selector based on an element
// https://source.chromium.org/chromium/chromium/src/+/main:third_party/devtools-frontend/src/front_end/panels/elements/DOMPath.ts?q=case:yes%20file:DOMPath.%5Btj%5Ds&ss=chromium

export const cssPath = function (
  node: Node,
  { optimized = true, idAttrs = [] as string[] } = {},
): string {
  if (!(node instanceof HTMLElement)) {
    return '';
  }

  const steps = [];
  let contextNode: Node | null = node;
  while (contextNode) {
    const step = cssPathStep(
      contextNode,
      optimized,
      contextNode === node,
      idAttrs,
    );
    if (!step) {
      break;
    } // Error - bail out early.
    steps.push(step);
    if (step.optimized) {
      break;
    }
    contextNode = contextNode.parentNode;
  }

  steps.reverse();
  return steps.join(' > ');
};

const cssPathStep = function (
  node: Node,
  optimized: boolean,
  isTargetNode: boolean,
  idAttrs: string[],
): Step | null {
  if (!(node instanceof HTMLElement)) {
    return null;
  }

  const id = node.getAttribute('id')?.trim() ?? '';
  const [{ idAttr, idAttrValue } = { idAttr: '', idAttrValue: '' }] =
    idAttrs.flatMap(attr =>
      (node.getAttribute(attr)?.trim() ?? '') !== ''
        ? [{ idAttr: attr, idAttrValue: node.getAttribute(attr) ?? '' }]
        : [],
    );
  if (optimized) {
    if (id !== '') {
      return new Step(idSelector(id), true);
    } else if (idAttr !== '') {
      return new Step(attrSelector(idAttr, idAttrValue), true);
    }

    const nodeNameLower = node.nodeName.toLowerCase();
    if (
      nodeNameLower === 'body' ||
      nodeNameLower === 'head' ||
      nodeNameLower === 'html'
    ) {
      return new Step(node.nodeName.toLowerCase(), true);
    }
  }
  const nodeName = node.nodeName.toLowerCase();

  if (id !== '') {
    return new Step(nodeName + idSelector(id), true);
  } else if (idAttr !== '') {
    return new Step(nodeName + attrSelector(idAttr, idAttrValue), true);
  }
  const parent = node.parentNode;
  if (!parent || parent.nodeType === Node.DOCUMENT_NODE) {
    return new Step(nodeName, true);
  }

  function prefixedElementClassNames(el: HTMLElement): string[] {
    const classAttribute = el.getAttribute('class')?.trim() ?? '';
    if (classAttribute !== '') {
      return [];
    }

    return classAttribute
      .split(/\s+/g)
      .filter(Boolean)
      .map(function (name) {
        // The prefix is required to store "__proto__" in a object-based map.
        return '$' + name;
      });
  }

  function idSelector(id: string): string {
    return '#' + CSS.escape(id);
  }

  function attrSelector(attr: string, value?: string) {
    if (value === undefined) {
      return `[${attr}]`;
    } else {
      return `[${attr}=${JSON.stringify(value)}]`;
    }
  }

  const prefixedOwnClassNamesArray = prefixedElementClassNames(node);
  let needsClassNames = false;
  let needsNthChild = false;
  let ownIndex = -1;
  let elementIndex = -1;
  const siblings = parent.childNodes;
  for (
    let i = 0;
    (siblings ?? undefined) !== undefined &&
    (ownIndex === -1 || !needsNthChild) &&
    i < siblings.length;
    ++i
  ) {
    const sibling = siblings[i];
    if (!(sibling instanceof HTMLElement)) {
      continue;
    }
    elementIndex += 1;
    if (sibling === node) {
      ownIndex = elementIndex;
      continue;
    }
    if (needsNthChild) {
      continue;
    }
    if (sibling.nodeName.toLowerCase() !== nodeName) {
      continue;
    }

    needsClassNames = true;
    const ownClassNames = new Set<string>(prefixedOwnClassNamesArray);
    if (!ownClassNames.size) {
      needsNthChild = true;
      continue;
    }
    const siblingClassNamesArray = prefixedElementClassNames(sibling);
    for (let j = 0; j < siblingClassNamesArray.length; ++j) {
      const siblingClass = siblingClassNamesArray[j];
      if (!ownClassNames.has(siblingClass)) {
        continue;
      }
      ownClassNames.delete(siblingClass);
      if (!ownClassNames.size) {
        needsNthChild = true;
        break;
      }
    }
  }

  let result = nodeName;
  if (
    isTargetNode &&
    nodeName.toLowerCase() === 'input' &&
    node.hasAttribute('type') &&
    !node.hasAttribute('id') &&
    !node.hasAttribute('class')
  ) {
    result += '[type=' + CSS.escape(node.getAttribute('type') ?? '') + ']';
  }
  if (needsNthChild) {
    result += ':nth-child(' + (ownIndex + 1) + ')';
  } else if (needsClassNames) {
    for (const prefixedName of prefixedOwnClassNamesArray) {
      result += '.' + CSS.escape(prefixedName.slice(1));
    }
  }

  return new Step(result, false);
};

export class Step {
  value: string;
  optimized: boolean;
  constructor(value: string, optimized: boolean) {
    this.value = value;
    this.optimized = optimized || false;
  }

  toString(): string {
    return this.value;
  }
}
